
"use client";

import { useState, useEffect } from 'react';
import { db } from "@/lib/firebase";
import { collection, onSnapshot } from "firebase/firestore";
import {
  Card,
  CardContent,
  CardDescription,
  CardFooter,
  CardHeader,
  CardTitle,
} from "@/components/ui/card";
import { Button } from "@/components/ui/button";
import { Badge } from "@/components/ui/badge";
import {
  FileText,
  User,
  Calendar,
  Clock,
  Loader2,
  CheckCircle,
  XCircle,
} from "lucide-react";
import { TeacherRequest } from "@/lib/types";
import { useToast } from "@/hooks/use-toast";
import { format } from "date-fns";

export default function RequestsPage() {
  const [requests, setRequests] = useState<TeacherRequest[]>([]);
  const [loading, setLoading] = useState(true);
  const { toast } = useToast();

  useEffect(() => {
    setLoading(true);
    const unsub = onSnapshot(collection(db, 'requests'), (snapshot) => {
        const allRequests = snapshot.docs.map(doc => ({ id: doc.id, ...doc.data() } as TeacherRequest));
        setRequests(allRequests);
        setLoading(false);
    }, (error) => {
        console.error("Error fetching requests: ", error);
        setLoading(false);
    });

    return () => unsub();
  }, []);

  const handleAction = (requestId: string, action: "approved" | "denied") => {
    console.log(`Request ${requestId} has been ${action}.`);
    toast({
      title: `Request ${action}`,
      description: `Request ID ${requestId} has been successfully ${action}.`,
    });
  };

  const getRequestTitle = (request: TeacherRequest) => {
    switch (request.type) {
      case "remove-student":
        return `Request to Remove Student`;
      case "change-time":
        return `Request to Change Time`;
      default:
        return `New Request`;
    }
  };

  const pendingRequests = requests.filter((r) => r.status === "pending");

  return (
    <div className="space-y-6">
      <div className="flex items-center gap-4">
        <FileText className="h-8 w-8 text-primary" />
        <h1 className="text-3xl font-bold font-headline">Teacher Requests</h1>
      </div>

      {loading ? (
        <div className="flex justify-center items-center h-64">
          <Loader2 className="h-8 w-8 animate-spin text-primary" />
        </div>
      ) : pendingRequests.length === 0 ? (
        <Card>
          <CardHeader>
            <CardTitle>No Pending Requests</CardTitle>
          </CardHeader>
          <CardContent>
            <p className="text-muted-foreground">
              There are no pending requests from teachers at this time.
            </p>
          </CardContent>
        </Card>
      ) : (
        <div className="grid gap-6 md:grid-cols-2 lg:grid-cols-3">
          {pendingRequests.map((request) => (
            <Card key={request.id} className="flex flex-col">
              <CardHeader>
                <CardTitle className="flex justify-between items-start">
                  <span>{getRequestTitle(request)}</span>
                  <Badge variant={request.type === 'remove-student' ? 'destructive' : 'secondary'}>
                    {request.type.replace("-", " ")}
                  </Badge>
                </CardTitle>
                <CardDescription>
                  Submitted on{" "}
                  {format(new Date(request.date), "MMMM dd, yyyy")}
                </CardDescription>
              </CardHeader>
              <CardContent className="flex-grow space-y-4 text-sm">
                <div className="flex items-start gap-3">
                  <User className="w-4 h-4 mt-1 text-muted-foreground" />
                  <div>
                    <p className="font-semibold">{request.details.studentName}</p>
                    <p className="text-muted-foreground">Student</p>
                  </div>
                </div>
                <div className="flex items-start gap-3">
                  <User className="w-4 h-4 mt-1 text-muted-foreground" />
                  <div>
                    <p className="font-semibold">{request.teacherName}</p>
                    <p className="text-muted-foreground">Teacher</p>
                  </div>
                </div>
                <div className="flex items-start gap-3">
                  <Calendar className="w-4 h-4 mt-1 text-muted-foreground" />
                  <div>
                    <p className="font-semibold">{request.details.day}</p>
                    <p className="text-muted-foreground">Class Day</p>
                  </div>
                </div>
                <div className="flex items-start gap-3">
                  <Clock className="w-4 h-4 mt-1 text-muted-foreground" />
                  <div>
                    <p className="font-semibold">{request.details.sessionTime}</p>
                    <p className="text-muted-foreground">Class Time</p>
                  </div>
                </div>
                <div className="border-l-2 pl-3 ml-1.5">
                    <p className="font-semibold text-muted-foreground">Reason:</p>
                    <p className="italic">"{request.details.reason}"</p>
                </div>
              </CardContent>
              <CardFooter className="flex gap-2">
                <Button
                  className="w-full"
                  onClick={() => handleAction(request.id, "approved")}
                >
                  <CheckCircle /> Approve
                </Button>
                <Button
                  variant="destructive"
                  className="w-full"
                  onClick={() => handleAction(request.id, "denied")}
                >
                  <XCircle /> Deny
                </Button>
              </CardFooter>
            </Card>
          ))}
        </div>
      )}
    </div>
  );
}
